.section-error {
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 10px;
	padding-bottom: 70px;
	color: #333;
	background-color: #dcdcdc;
}

.section-error:after {
	content: '';
	display: block;
	clear: both;
}

.section-error > .inner {
	width: 100%;
	max-width: 880px;
	margin: 0 auto;
	padding: 70px 0;
	text-align: center;
	background-color: #fff;
}

.section-error > .inner:after {
	content: '';
	display: block;
	clear: both;
}

.section-error .bg-error {
	width: 267px;
	height: 279px;
	margin: 0 auto 35px;
	background-image: url("../images/error.png");
}

.section-error h3 {
	margin-bottom: 25px;
	font-size: 23px;
	font-weight: 700;
	text-transform: uppercase;
}

.section-error p {
	margin-bottom: 45px;
	font-size: 18px;
	font-weight: 400;
	line-height: 1.3;
}

.section-error .btn-go-home {
	width: 240px;
	height: 60px;
	font-size: 18px;
	font-weight: 400;
}
